/* src/index.css 또는 메인 CSS 파일 */

@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom animations and consistent styles */
@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fade-in {
  animation: fade-in 0.8s ease-out;
}

/* Consistent button styles */
.btn-primary {
  @apply bg-gradient-to-r from-indigo-600 to-purple-600 text-white font-bold py-3 px-6 rounded-lg hover:from-indigo-700 hover:to-purple-700 transition-all shadow-md hover:shadow-lg;
}

/* Consistent card styles */
.card {
  @apply bg-white rounded-2xl shadow-lg p-6 transition-all duration-300 hover:shadow-xl;
}

/* Consistent section headers */
.section-header {
  @apply text-2xl sm:text-3xl font-bold text-gray-800 mb-6 sm:mb-8 text-center;
}

/* Consistent gradient backgrounds */
.gradient-bg {
  @apply bg-gradient-to-br from-indigo-50 to-purple-50;
}